@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.inicio-container{
    background-image: url(/ledmebe/static/media/sillon.427efd09.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 70em;
    display: flex;
}
.inicio-logo{
    margin: 5%;
    height: 25em;
    width: 25em;
    border-radius: 50%;
}
.inicio-logo:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: 1s;
}
.inicio-titulo{
    text-align: center;
    font-family: 'Roboto Slab', serif;
    margin-top: 10%;
    margin-left: 10%;
    margin-bottom: 10em;
    align-self: center;
}

@media (max-width: 600px) {
    .inicio-logo{
        height: 7em;
        width: 7em;
    }
    .logo{
        display: none;
    }
    div.container{
        justify-content: center !important;
    }
    .navbar-toggler{
        margin-right: 2.5em;
        border: none;
    }
    .inicio-container{
        flex-direction: column  ;
        padding-top: 13em;
    }
    .inicio-titulo{
        margin-bottom: 0;
        margin-left: 0;
        font-size: 2em;
    }
}

.widget-box{
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
}
.cart-widget{
    margin: 0;
    width: 30px;
    background-color: red;
    color: white;
    padding: 0.6em;
    font-size: 0.7em;
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
    border-bottom-right-radius: 15px;
    border-bottom-left-radius: 15px;

}
.containerStyles{
    display: flex;
    justify-content: space-evenly;
    margin: 5%;
    flex-wrap: wrap;
    
}
.styles{
    height: 2em;
    width: 100%;
    font-size: 1.7em;

}
.arrowStyles{
    width: 1.5em;
    height: 1.5em;
    text-align: left;
    color: #22c9bb;
}
.cart{
    display: flex;
    flex-direction: column;
    font-family: 'Roboto Slab', serif;
}
@media (max-width: 600px){
    .cart{
        margin-top: 5em;
    }
    .cart-title{
        margin: 0;
    }
}
.logo{
    border-radius: 50%;
    height: 3.5em;
    width: 3.5em;
  }
.link{
  font-size: 1.3em;
  font-family: 'Roboto Slab', serif;
}
.link:hover{
  border-bottom: 1px solid steelblue ;
  
}
.link-sub{
  font-family: 'Roboto Slab', serif;
}
.item-link{
    text-decoration: none;
    color: black;
}
button.item-link:hover{
    color: white;
}
.left-arrow{
    text-align: left;
}
.img-size{
    height: 15em;
}
.detail-products{
    width: 100%;
    margin: 3em;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    align-content: space-around;
}
@media (max-width: 600px){
    .detail-products{
        margin: 0% !important;
    }
}
.products{
    display: flex;
}
.productsBtn{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.detail-products{
    width: 100%;
    margin: 3em;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    align-content: space-around;
}
.header{
    background-image: url(/ledmebe/static/media/sillon.427efd09.jpg);
    font-family: 'Roboto Slab', serif;
}
div span {
    margin: 2em;
}
.itemDetail-link{
    text-decoration: none;
    color: white; 
}
.itemDetail-container{
    display: flex;
    font-family: 'Roboto Slab', serif;

}
.detail-img{
    width: 30em;
    height: 30em;
}
.left-arrow{
    margin: 1em;
}
.itemDetail-title{
    margin: 2em;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-left: 8em;
}
.detail-line{
    width: 150%;
    height: 1.5px;
    color: rgb(255, 166, 0);
}
@media (max-width: 600px){
    .itemDetail-container{
        flex-direction: column;
    }
    .detail-line{
        width: 100%;
    }
    .detail-img{
        height: 15em;
        width: 15em;
    }
    .itemDetail-title{
        margin-left: 3.7em;
        
    }
}
.container{
    display: flex;
    justify-content: center;
    margin-bottom: 5em;
    height: 100%;
}
.alert-danger{
    margin: 10%;
    font-size: 40px;
    width: 100%;
}
.container-sinstock{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.alert-compraID{
    margin: 2em;
    
}
.App {
  text-align: center;
  
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
a {
  text-decoration: none !important;
  color: black !important;
}

